<template>
	<section id="signin" class=" d-flex align-items-center">

    <div class="container">
      <div class="row">
        <div class="col-lg-7 d-flex flex-column justify-content-center">
          <div class="signin-lgo">
         <img src="../assets/images/workwire.png">
       </div>


        </div>

           <div class="col-lg-5 d-flex  justify-content-center align-items-center pt-4 ">

           <div class="signin-box">
            <div class="sub-text mt-5">
            <h4> Join Workshires</h4>
            <h5> Choose a display name</h5>
            <div class="join-workshire">
              <form action="" class="body" v-on:submit.prevent="LoginUser">
                 <div class="form-group">
		            <input type="text" placeholder="Enter Email" name="email" class="form-control" required v-model="post.email">
		          </div>
		          <div class="form-group">
		            <input type="password" placeholder="Enter Password" name="password" required class="form-control" v-model="post.password">
        			<p v-if="errorMessage" style="color:red;">{{ errorMessage }}</p>
		          </div>
            </form>

            <div class="box-join">
            	<button type="submit" class="join-signin">Join</button>
            
         	</div>
         <div class="botom-trms">
            <p> By joining, you agree to Workshires’s <a href="#"> Terms 
of Service, </a>
as well as to receive occasional 
emails from us.</p>

</div>
</div>

<router-link to = "/register-form" class="alradt-acount"><label>Already have an account? </label> <span>Sign up.</span> <img src="../assets/images/back-arow.png"> </router-link>



           </div>

     </div>
           </div>


        <!-- <div class="col-lg-6 order-1 order-lg-2 hero-img" >
          <img src="image/sliderbg.png" class="img-fluid " alt="">
        </div> -->
      </div>
    </div>

  </section>


</template>
<script>
import {
    loginRequest
} from './ApiFunctions/index';

export default {
  name: "SigninForm",
  data() {
        return {
            errorMessage: '',
            post: {
                email: '',
                password: ''
            }
        }
    },
    methods: {
        async LoginUser() {
            const res = await loginRequest('/login-user', this.post)
            if (res.auth) {
                this.$router.push({
                    name: 'UserTable',
                    path: '/user'
                })
            } else if (res.error) {
                this.errorMessage = res.error;
            } else {
                this.errorMessage = '';
                this.$router.push({
                    name: 'SignupForm',
                    path: '/sign-up'
                })

            }

        }
    }
}
</script>


